@-webkit-keyframes fadeIn { from { opacity:0; } to { opacity:1; } }

@keyframes fadeIn { from { opacity:0; } to { opacity:1; } }
 
.fade-in {
   opacity:0;  /* make things invisible upon start */
   -webkit-animation:fadeIn ease-in 1;
           animation:fadeIn ease-in 1;
   -webkit-animation-fill-mode:forwards;
           animation-fill-mode:forwards;
   -webkit-animation-duration:1.4s;
           animation-duration:1.4s;
  }

  @-webkit-keyframes rightFadeInOut {
    0% {
      position: absolute;
      right: -5vw;
      opacity: 0;
    }
  
    100% {
      position: absolute;
      right: 0vw;
      opacity: 1;
    }
  }

  @keyframes rightFadeInOut {
    0% {
      position: absolute;
      right: -5vw;
      opacity: 0;
    }
  
    100% {
      position: absolute;
      right: 0vw;
      opacity: 1;
    }
  }



  @-webkit-keyframes leftFadeInOut {
    0% {
      position: absolute;
      left: -5vw;
      opacity: 0;
    }
  
    100% {
      position: absolute;
      left: 2vw;
      opacity: 1;
    }
  }



  @keyframes leftFadeInOut {
    0% {
      position: absolute;
      left: -5vw;
      opacity: 0;
    }
  
    100% {
      position: absolute;
      left: 2vw;
      opacity: 1;
    }
  }

  .rw-words{
    display: inline;
    text-indent: 10px;
  }
/*   
  .rw-words span{
    position: absolute;
    opacity: 0;
    overflow: hidden;
    width: 600%;
    text-align: left;
    color: #7851a9;
  } */
  
  .rw-words-1 span{
    -webkit-animation: rotateWordsFirst 12s linear infinite 0s;
            animation: rotateWordsFirst 12s linear infinite 0s;
  }
  
  .rw-words span:nth-child(2) { 
    -webkit-animation-delay: 3s; 
            animation-delay: 3s; 
    color: #7851a9;
  }
  .rw-words span:nth-child(3) { 
    -webkit-animation-delay: 6s; 
            animation-delay: 6s; 
    color: #7851a9;	
  }
  .rw-words span:nth-child(4) { 
    -webkit-animation-delay: 9s; 
            animation-delay: 9s; 
    color: #7851a9;
  }
  /* .rw-words span:nth-child(5) { 
    animation-delay: 12s; 
    color: #7851a9;
  }
  .rw-words span:nth-child(6) {  
    animation-delay: 15s; 
    color: #7851a9;
  } */
  
  @-webkit-keyframes rotateWordsFirst {
      0% { opacity: 1; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in; height: 0px; }
      8% { opacity: 1; height: 60px; }
      19% { opacity: 1; height: 60px; }
    25% { opacity: 0; height: 60px; }
      100% { opacity: 0; }
  }
  
  @keyframes rotateWordsFirst {
      0% { opacity: 1; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in; height: 0px; }
      8% { opacity: 1; height: 60px; }
      19% { opacity: 1; height: 60px; }
    25% { opacity: 0; height: 60px; }
      100% { opacity: 0; }
  }
  
  @-webkit-keyframes rotateWordsSecond {
      0% { opacity: 1; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in; width: 0px; }
      10% { opacity: 0.3; width: 0px; }
    20% { opacity: 1; width: 100%; }
      27% { opacity: 0; width: 100%; }
      100% { opacity: 0; }
  }
  
  @keyframes rotateWordsSecond {
      0% { opacity: 1; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in; width: 0px; }
      10% { opacity: 0.3; width: 0px; }
    20% { opacity: 1; width: 100%; }
      27% { opacity: 0; width: 100%; }
      100% { opacity: 0; }
  }

