@-webkit-keyframes bounce{
    0%, 20%, 50%, 80%, 100%{-webkit-transform:translateY(0);transform:translateY(0);}
    40% {-webkit-transform:translateY(-0.5vh);transform:translateY(-0.5vh);}
    60%{-webkit-transform:translateY(-0.25vh);transform:translateY(-0.25vh);}
}

@keyframes bounce{
    0%, 20%, 50%, 80%, 100%{-webkit-transform:translateY(0);transform:translateY(0);}
    40% {-webkit-transform:translateY(-0.5vh);transform:translateY(-0.5vh);}
    60%{-webkit-transform:translateY(-0.25vh);transform:translateY(-0.25vh);}
}
